import React from "react";
import "./Error.css";
import errorPng from "../../assets/error.png";
import { Link } from "react-router-dom";
import AOS from "aos/dist/aos";
import "aos/dist/aos.css";

const Error = () => {
  AOS.init({
    duration: 1000,
    offset: 120,
    easing: "ease-in-out",
  });
  return (
    <>
      <div className="errorWrapper" data-aos="fade-up">
        <img src={errorPng} className="errorPng" />
        <p className="errorParagraph">
          <span className="text">
            It seems like we were not able to find the page that you were
            looking for
          </span>
          <br />
          <br />
          <span className="text">
            Dont worry, everything is cool, you can head back home
          </span>
          <br />
          <br />
          <Link to="/" className="backHome">
            Back Home
          </Link>
        </p>
      </div>
    </>
  );
};

export default Error;
