import React from "react";
import "./Footer.css";
import { data } from "./data";

function Footer() {
  const instaUrl = "https://instagram.com/grady.css/";
  return (
    <>
      <footer>
        <h3 className="workWithMe">Would you like to work with me ?</h3>
        <h4 className="contactMe">Contact me at +919619733084</h4>
        <hr className="footerHr" />
        <h4 className="socialProfiles">Or contact me through socials</h4>
        <ul className="socialUl">
          {data.map((item, index) => {
            return (
              <li
                className={item.cName}
                key={index}
                onClick={() => window.open(item.url)}
              >
                <i className={item.iconClass}></i>
              </li>
            );
          })}
        </ul>
        <h4 className="copyrightInfo">&copy; Grady's Corner</h4>
      </footer>
    </>
  );
}

export default Footer;
