import React from "react";
import Typical from "react-typical";
import "./Home.css";
import Logo from "../../assets/img.png";
import Projects from "../../components/Projects/Projects";
import AOS from "aos/dist/aos";
import "aos/dist/aos.css";

const Home = () => {
  const githubURL = "https://github.com/grady2002";
  const text = [
    "Frontend Developer",
    2000,
    "UI Designer",
    2000,
    "Design Consultant",
    2000,
    "Software Developer",
    2000,
  ];
  AOS.init({
    duration: 1000,
    offset: 120,
    easing: "ease-in-out",
  });

  return (
    <>
      <div className="home" data-aos="fade-up">
        <div className="innerFlex f1">
          <h3 className="homeHeader">I am Aatif Mastan ,</h3>
          <span className="typing">
            <Typical className="typical" loop={Infinity} steps={text} />
          </span>
          <p className="homeParagraph">
            Hey, I am a front-end web developer and a python coder. I specialize
            in simple and logical coding. My codes and designs are guaranteed to
            work well on all devices (Windows, Android, Linux, Mac). I give life
            to my websites by adding a little special touch of animation like
            the typewriter effect used above and thats what makes them unique.
          </p>
          <button className="homeChat" onClick={() => window.open(githubURL)}>
            Checkout My Software Projects
          </button>
        </div>
        <img className="innerFlex landingIMG" src={Logo} />
      </div>
      <Projects />
    </>
  );
};

export default Home;
