import React from "react";
import "./Projects.css";
import { data } from "./data";
import AOS from "aos/dist/aos";
import "aos/dist/aos.css";

const Projects = () => {
  AOS.init({
    duration: 1000,
    offset: 120,
    easing: "ease-in-out",
  });

  return (
    <>
      <div className="projectsWrapper" data-aos="fade-up">
        <h3 className="myProjects">Works that make me feel proud !</h3>
        <div className="projectsGrid">
          {data.map((project, index) => {
            return (
              <div className={project.cName} key={index}>
                <h3 className={project.headercName}>{project.title}</h3>
                <img
                  className={project.imgcName}
                  src={project.path}
                  onClick={() => window.open(project.url)}
                />
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default Projects;
