import React, { useState } from "react";
import "./Navbar.css";
import { Link } from "react-router-dom";
import { data } from "./data";

const Navbar = () => {
  const [isClass, setIsClass] = useState(false);
  const setClassName = () => setIsClass(!isClass);
  return (
    <>
      <nav className="topNav">
        <h3 className="topHeader">Welcome !</h3>
        <i
          className={isClass ? "topIcon fas fa-times" : "topIcon fa fa-bars"}
          onClick={setClassName}
        ></i>
      </nav>
      <aside className={isClass ? "sideBar" : "sideBar hidden"}>
        <ul className="sideBarUl">
          {data.map((item) => {
            return (
              <li key={item.id} className={item.cName}>
                <Link to={item.url} onClick={setClassName}>
                  <i className={item.iconClass}></i>
                  {item.title}
                </Link>
              </li>
            );
          })}
        </ul>
      </aside>
    </>
  );
};

export default Navbar;
